//    ( )
const StatusBadge = memo(({ purchase }: { purchase: Purchase }) => {
  const status = purchase.is_received 
    ? 'completed'
    : (purchase.middle_manager_status === 'approved' && purchase.final_manager_status === 'approved')
    ? 'inProgress'
    : (purchase.middle_manager_status === 'rejected' || purchase.final_manager_status === 'rejected')
    ? 'rejected'
    : 'pending';
  
  const config = {
    completed: { text: '', className: 'bg-green-100 text-green-800' },
    inProgress: { text: '', className: 'bg-hansl-100 text-hansl-800' },
    rejected: { text: '', className: 'bg-red-100 text-red-800' },
    pending: { text: '', className: 'bg-yellow-100 text-yellow-800' }
  };
  
  const { text, className } = config[status];
  return <Badge className={className}>{text}</Badge>;
});

StatusBadge.displayName = 'StatusBadge';

//     
const getCurrencySymbol = (currency: string) => {
  if (!currency) return '';
  if (['KRW', '', ''].includes(currency)) return '';
  if (['USD', '$', ''].includes(currency)) return '$';
  if (['EUR', ''].includes(currency)) return '';
  if (['JPY', '', ''].includes(currency)) return '';
  if (['CNY', '', ''].includes(currency)) return '';
  return currency;
};

